      </h3>
      <div class="p-3 bg-light rounded-lg">
        <div
          v-for="(log, index) in pageMsg"
          :key="index"
        >
          {{ log }}
        </div>
      </div>
    </div>

    <showcode>
      <v-code>
        <pre>
        &lt;v-page :total-row="21" align="center" @page-change="pageChange"&gt;
        &lt;/v-page&gt;
        </pre>
