    <div class="d-flex flex-wrap border px-3 pt-3 pb-2 mb-3 rounded-lg shadow-sm">
      <div
        v-for="(num, index) in pageArr"
        :key="index"
        class="rounded-lg bg-light me-2 mb-2 text-black-50 display-3
        d-flex justify-content-center align-items-center"
        style="width: 10.9rem; height: 8rem;"
      >
        {{ num }}
      </div>
    </div>
    <div>
      <v-page
        :total-row="arr.length"
        align="left"
        @page-change="pagePhotoChange"
      />
    </div>

    <showcode>
      <v-code>
        <pre>
        &lt;template&gt;
          &lt;div&gt;
            &lt;div class="photo-wall"&gt;
              &lt;div v-for="num in pageArr" v-html="num" class="brick"&gt;&lt;/div&gt;
            &lt;/div&gt;
            &lt;v-page :total-row="arr.length" @page-change="pagePhotoChange"&gt;
            &lt;/v-page&gt;
          &lt;/div&gt;
        &lt;/template&gt;

        &lt;script&gt;
        export default {
          data () {
            return {
              arr: Array(108).fill(0).map((val, index) => index + 1),
              pageArr: []
            }
          },
          methods: {
            pagePhotoChange (pInfo) {
              let start=0, end=0
              start = pInfo.pageSize * (pInfo.pageNumber-1)
              end = start + pInfo.pageSize
              if(end > this.arr.length) end = this.arr.length

              this.pageArr.splice(0, this.pageArr.length)
              for (let i = start; i &lt; end; i++) {
                this.pageArr.push(this.arr[i])
              }
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

    <h4 class="mt-5">
      <i18n path="page.demo.complete" />
    </h4>
    <v-page
      align="left"
      :total-row="100"
      v-model="current"
      ref="page"
    />

    <div class="my-2 d-flex">
      <div class="col-md-1 me-3">
        <input
          type="text"
          class="form-control"
          v-model="target"
        >
      </div>
      <button
        class="btn btn-secondary me-3"
        type="button"
        @click="go"
      >
        forward
      </button>
      <button
        class="btn btn-outline-secondary"
        type="button"
        @click="current = current + 1"
      >
        page + 1 (v-model)
      </button>
    </div>

    <showcode>
      <v-code>
        <pre>
        &lt;v-page align="left" :total-row="100" v-model="current" ref="page"&gt;&lt;/v-page&gt;

        &lt;button class="btn btn-secondary" type="button" @click="go"&gt;forward&lt;/button&gt;
        &lt;button
          class="btn btn-outline-secondary"
          type="button"
          @click="current = current + 1"
        &gt;page + 1&lt;/button&gt;

        &lt;script&gt;
        export default {
          data () {
            return {
              current: 3
            }
          },
          methods: {
            go () {
              // use api to change current page
              this.$refs.page.goPage(3)
            }
          }
        }
        &lt;/script&gt;
        </pre>
      </v-code>
    </showcode>

