        &lt;v-page
          :total-row="100"
          :border="true"
          v-slot="{ pageNumber, pageSize, totalPage, totalRow, isFirst, isLast }"
        &gt;
          &lt;!-- scoped slot will output 6 page states --&gt;
          &lt;div&gt;
            page number: &lt;span v-text="pageNumber"&gt;&lt;/span&gt;
            page size: &lt;span v-text="pageSize"&gt;&lt;/span&gt;
            total page: &lt;span v-text="totalPage"&gt;&lt;/span&gt;
            total row: &lt;span v-text="totalRow"&gt;&lt;/span&gt;
            is first page: &lt;span v-text="isFirst"&gt;&lt;/span&gt;
            is last page: &lt;span v-text="isLast"&gt;&lt;/span&gt;
